export default {
    data: () => ({
        init: true,
        result: null
    }),
    methods: {
        mixinfn() {
            console.log("from mixins");
        },
        http_post() {
            const baseURI = `https://jsonplaceholder.typicode.com/photos?_limit=10`;
            this.$axios.get(baseURI).then((result) => {
                this.result = result.data;
            });
            return this.result
        },
    },
    mounted() {},
}